<template>
  <svg
  	@click="clickSvg"
    :class="getClasses" 
    :style="getStyle" 
    viewBox="0 0 128 128" 
    version="1.1" 
    xmlns="http://www.w3.org/2000/svg" 
    xmlns:xlink="http://www.w3.org/1999/xlink" 
    :width="width" 
    :height="height"
  >
    <path d="M1.585 12.087c0 6.616 3.974 11.98 8.877 11.98 4.902 0 8.877-5.364 8.877-11.98 0-6.616-3.975-11.98-8.877-11.98-4.903 0-8.877 5.364-8.877 11.98zM125.86.107H35.613c-1.268 0-2.114 1.426-2.114 2.852v18.255c0 1.712 1.057 2.853 2.114 2.853h90.247c1.268 0 2.114-1.426 2.114-2.853V2.96c0-1.711-1.057-2.852-2.114-2.852zM.106 62.86c0 6.615 3.974 11.979 8.876 11.979 4.903 0 8.877-5.364 8.877-11.98 0-6.616-3.974-11.98-8.877-11.98-4.902 0-8.876 5.364-8.876 11.98zM124.17 50.88H33.921c-1.268 0-2.114 1.425-2.114 2.851v18.256c0 1.711 1.057 2.852 2.114 2.852h90.247c1.268 0 2.114-1.426 2.114-2.852V53.73c0-1.426-.846-2.852-2.114-2.852zM.106 115.913c0 6.616 3.974 11.98 8.876 11.98 4.903 0 8.877-5.364 8.877-11.98 0-6.616-3.974-11.98-8.877-11.98-4.902 0-8.876 5.364-8.876 11.98zm124.064-11.98H33.921c-1.268 0-2.114 1.426-2.114 2.853v18.255c0 1.711 1.057 2.852 2.114 2.852h90.247c1.268 0 2.114-1.426 2.114-2.852v-18.255c0-1.427-.846-2.853-2.114-2.853z"></path>
  </svg>
</template>

<script>

export default {
	name: 'svg-icon',
	props: {
		width: {
			type: Number,
			default: 32
		},
		height: {
			type: Number,
			default: 32
		},
		classes: Array,
		styles: Object
	},
	computed: {
		getStyle: function () {
			let stStyle = ''
			if (this.styles != undefined)
				for(let st in this.styles)
					stStyle += `${st}: ${this.styles[st]};`
				
			return stStyle
		},
		getClasses: function () {
			let stClass = 'icon'

			if (this.classes != undefined)
				for(let st in this.classes)
					stClass += `, ${this.classes[st]}`

			return stClass
		}
	},
	methods: {
		clickSvg(){
			this.$emit('clik-svg')
		}
	}
}
</script>




